___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --header-color: #00458b;
  --header-text-color: #e6f7ff;
  --body-color: #f4fbff;
  --form-text-color: #003264;
  --border-color: #00458b;
  --submit-color: #00458b;
  --submit-color-hover: #006bd5;
  --error-text-color: #C7253E;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input,
select,
button {
  display: block;
  font-family: inherit;
  font-size: 100%;
  width: 100%;
  border: none;
  background-color: inherit;
  color: inherit;
}

body {
  background-color: var(--body-color);
  min-height: 100vh;
  width: 100%;

  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: 
    "header"
    "form"
    "footer";
}

header {
  grid-area: header;
  text-align: center;
  padding: 1rem;
  font-size: 2rem;
  background-color: var(--header-color);
  color: var(--header-text-color);
}

.form-container {
  grid-area: form;
  height: 100%;
  padding: 2rem;
  padding-top: 5rem;
  
  display: flex;
  justify-content: center;
}

.form-container form {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  gap: 1rem;

  height: fit-content;
  border-radius: 8px;
  border: 2px solid var(--form-text-color);
  padding: 2rem;

  font-size: 1.1rem;
  font-weight: 600;
  background-color: var(--form-color);
  color: var(--form-text-color);
}

.form-container form label {
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 1fr 2fr;
  grid-template-areas: 
    "label input"
    "empty error";
  align-items: center;

  padding-right: 2rem;
}

.form-container form label .span-label {
  padding-right: 1rem;
  text-align: end;
}

.form-container form label .span-error {
  display: none;
  padding-top: 0.3rem;
  grid-area: error;
  color: var(--error-text-color);
}

.form-container form label .span-error.show {
  display: block;
}

.form-container form input,
.form-container form select {
  grid-area: input;
  border: 2px solid var(--border-color);
  border-radius: 8px;
  padding: 0.2rem 0.5rem;
}

.form-container form input.interacted:invalid,
.form-container form select.interacted:invalid {
  border: 2px solid var(--error-text-color);
}

.form-container form button {
  align-self: center;
  cursor: pointer;
  max-width: 15rem;
  background-color: var(--submit-color);
  padding: 1rem;
  border-radius: 8px;
  color: white;
}

.form-container form button:hover {
  background-color: var(--submit-color-hover);
}

footer {
  background-color: var(--header-color);
  grid-area: footer;
  width: 100%;
  text-align: center;
  padding: 1rem;
}

footer img {
  height: 2rem;
  width: 2rem;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;EACE,uBAAuB;EACvB,4BAA4B;EAC5B,qBAAqB;EACrB,0BAA0B;EAC1B,uBAAuB;EACvB,uBAAuB;EACvB,6BAA6B;EAC7B,2BAA2B;AAC7B;;AAEA;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;AACxB;;AAEA;;;EAGE,cAAc;EACd,oBAAoB;EACpB,eAAe;EACf,WAAW;EACX,YAAY;EACZ,yBAAyB;EACzB,cAAc;AAChB;;AAEA;EACE,mCAAmC;EACnC,iBAAiB;EACjB,WAAW;;EAEX,aAAa;EACb,iCAAiC;EACjC;;;YAGU;AACZ;;AAEA;EACE,iBAAiB;EACjB,kBAAkB;EAClB,aAAa;EACb,eAAe;EACf,qCAAqC;EACrC,+BAA+B;AACjC;;AAEA;EACE,eAAe;EACf,YAAY;EACZ,aAAa;EACb,iBAAiB;;EAEjB,aAAa;EACb,uBAAuB;AACzB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,gBAAgB;EAChB,SAAS;;EAET,mBAAmB;EACnB,kBAAkB;EAClB,wCAAwC;EACxC,aAAa;;EAEb,iBAAiB;EACjB,gBAAgB;EAChB,mCAAmC;EACnC,6BAA6B;AAC/B;;AAEA;EACE,aAAa;EACb,6BAA6B;EAC7B,8BAA8B;EAC9B;;iBAEe;EACf,mBAAmB;;EAEnB,mBAAmB;AACrB;;AAEA;EACE,mBAAmB;EACnB,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,gBAAgB;EAChB,8BAA8B;AAChC;;AAEA;EACE,cAAc;AAChB;;AAEA;;EAEE,gBAAgB;EAChB,qCAAqC;EACrC,kBAAkB;EAClB,sBAAsB;AACxB;;AAEA;;EAEE,yCAAyC;AAC3C;;AAEA;EACE,kBAAkB;EAClB,eAAe;EACf,gBAAgB;EAChB,qCAAqC;EACrC,aAAa;EACb,kBAAkB;EAClB,YAAY;AACd;;AAEA;EACE,2CAA2C;AAC7C;;AAEA;EACE,qCAAqC;EACrC,iBAAiB;EACjB,WAAW;EACX,kBAAkB;EAClB,aAAa;AACf;;AAEA;EACE,YAAY;EACZ,WAAW;AACb","sourcesContent":[":root {\r\n  --header-color: #00458b;\r\n  --header-text-color: #e6f7ff;\r\n  --body-color: #f4fbff;\r\n  --form-text-color: #003264;\r\n  --border-color: #00458b;\r\n  --submit-color: #00458b;\r\n  --submit-color-hover: #006bd5;\r\n  --error-text-color: #C7253E;\r\n}\r\n\r\n* {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: border-box;\r\n}\r\n\r\ninput,\r\nselect,\r\nbutton {\r\n  display: block;\r\n  font-family: inherit;\r\n  font-size: 100%;\r\n  width: 100%;\r\n  border: none;\r\n  background-color: inherit;\r\n  color: inherit;\r\n}\r\n\r\nbody {\r\n  background-color: var(--body-color);\r\n  min-height: 100vh;\r\n  width: 100%;\r\n\r\n  display: grid;\r\n  grid-template-rows: auto 1fr auto;\r\n  grid-template-area